.intro_1{
  display: flex;  
}
.intro_content{
  border: 2px solid skyblue;
  border-radius:10px;
}
.list{
  width: 400px;
  margin: 30px;
  border: 2px solid skyblue;
  border-radius:10px;
  padding:5px 10px;
}

.intro_1 img{
  padding-right: 100px;
}

@media screen and (max-width:900px){
  .intro_1{
    display: block;
  }

  .intro_1 img{
    display: none;
  }
}